.card {
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 1rem;
    padding: 1rem;
  }

  &__name {
    display: flex;
    align-items: center;
  }

  &__desc {
    color: rgba(0, 0, 0, 0.5);
    font-size: 12px;
    text-align: left;
    max-width: 12rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}