@use '../../../../variables';

.page-search {
  padding: 0 .5rem;

  &__input {
    background-color: variables.$bg-color;
    height: 100%;
  }

  &__title {
    font-weight: bold;
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }

  &__desc {
    font-size: .9rem;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 2rem;
  }
}